.burger-button {
	display: none;
	color: $gray-800;
	font-size: 30px;
	line-height: 1;
	cursor: pointer;
	z-index: 2;

	&::after {
		content: '‚ò∞';
		display: block;
		text-align: center;
	}

	&:hover,
	&:focus {
		color: $primary;
	}

	&_opened {
		font-size: 24px;

		&::after {
			content: '‚úï';
		}
	}
}

@media screen and (max-width: 991px) {
	.burger-button {
		display: inline-block;
	}
}
